import {
  StreamlitComponentBase,
  withStreamlitConnection,
} from "streamlit-component-lib"
import React, { ReactNode } from "react"
// new imports
import Card from '@mui/material/Card';
import CardHeader from '@mui/material/CardHeader';
import CardContent from '@mui/material/CardContent';
import { Typography, CardActions } from "@mui/material";
import IconButton from '@mui/material/IconButton';
import LaunchIcon from '@mui/icons-material/Launch';


// the `render()` function is called when component is re-rendered
class CardComponent extends StreamlitComponentBase<any> {

  public render = (): ReactNode => {
    // Arguments that are passed to the plugin in Python are accessible
    // via `this.props.args`
    const title = this.props.args["title"]
    const subtitle = this.props.args["subtitle"]
    const body = this.props.args["body"]
    const link = this.props.args["link"]

    return (
      <div style={{ padding: "0.5rem" }}>
        <Card variant="outlined">
          <CardHeader
            title={title}
            subheader={subtitle}
          />
          <CardContent>
            <Typography variant="body2">
              {body}
            </Typography>
          </CardContent>
          <CardActions disableSpacing>
            <IconButton
              aria-label="open url"
              href={link}
              target="_PARENT"
            >
              <LaunchIcon />
            </IconButton>
          </CardActions>
        </Card>
      </div>
    )
  }
}

// connection between component and Streamlit
export default withStreamlitConnection(CardComponent)